


 html {
  line-height: 1.15; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
}



body {
  margin: 0;
}


main {
  display: block;
}


h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

hr {
  box-sizing: content-box; /* 1 */
  height: 0; /* 1 */
  overflow: visible; /* 2 */
}


pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}


a {
  background-color: transparent;
}


abbr[title] {
  border-bottom: none; /* 1 */
  text-decoration: underline; /* 2 */
  text-decoration: underline dotted; /* 2 */
}

b,
strong {
  font-weight: bolder;
}


code,
kbd,
samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}



small {
  font-size: 80%;
}



sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}



img {
  border-style: none;
  max-width: 100%;
}



button,
input,
optgroup,
select,
textarea {
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  line-height: 1.15; /* 1 */
  margin: 0; /* 2 */
}


button,
input { /* 1 */
  overflow: visible;
}



button,
select { /* 1 */
  text-transform: none;
}



button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}



button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}



button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}



fieldset {
  padding: 0.35em 0.75em 0.625em;
}



legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-width: 100%; /* 1 */
  padding: 0; /* 3 */
  white-space: normal; /* 1 */
}



progress {
  vertical-align: baseline;
}



textarea {
  overflow: auto;
}



[type="checkbox"],
[type="radio"] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
}



[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}



[type="search"] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}


[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}


::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}


details {
  display: block;
}


summary {
  display: list-item;
}


template {
  display: none;
}



[hidden] {
  display: none;
}












   nav {
     display: flex;
     justify-content: space-between;
     align-items: center;
     max-width: 90%;
     margin: 0 auto;
     width: 900px;
   }

   h1 {
    color: #1e1e1e;
   }

   p {
     color: #687B93;
     font-size: 18px;
     line-height: 22px;
   }

   button {
     background-color: #1e1e1e;
     border: none;
     color: white;
     padding: 12px 24px;
     font-size: 14px;
     letter-spacing: 0.03em;
   }

   nav a {
     color: #1e1e1e;
     text-decoration: none;
   }

   nav ul {
     list-style: none;
   }

  .wrapper {
    width: 900px;
    max-width: 90%;
    margin: 0 auto;
  }



#images {
  padding: 48px 0;
  background-color: #F4F4F7;
  margin: 48px 0;
  border-top: 2px solid black;
  border-bottom: 2px solid black;
}

.image {
  background-color: white;
  border: 2px solid black;
  padding: 48px;
  border-radius: 3px;
  margin-top: 48px;
}


.wrapper.images {
  max-width: 90%;
  margin: 0 auto;
  width: 900px;
  display: grid;
  grid-template-columns: 1fr;
}

footer {
  width: 1100px;
  max-width: 90%;
  margin: 0 auto;
}

footer ul {
  list-style: none;
  padding-left: 0;
  text-align: right;
  li {
    display: inline-block;
    padding-left: 8px;
    color: black;
  }
}


@media only screen and (min-width: 600px) {


  .wrapper.images {
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 32px;
  } 

  .image {
    margin: 0;
  }
  


}